import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {ITheme, IUser} from "../interfaces"
import {Observable} from "rxjs";
import {environment} from "../../environments/environment";
import { IArt } from '../interfaces/art';

const apiUrl = environment.apiUrl;

@Injectable({
  providedIn: 'root'
})
export class ThemeService {

  constructor(private http:HttpClient) { }

  loadThemeList(): Observable<IArt[]> {
    return this.http.get<IArt[]> (`${apiUrl}/artworks`);
  }

  loadThemeById(themeId : string): Observable<ITheme> {
    return this.http.get<ITheme> (`${apiUrl}/artworks/${themeId}`);
  }

  addTheme$(body: {themeName:string, postText:string}) : Observable<ITheme> {
    return this.http.post<ITheme> (`${apiUrl}/artworks`,body, {withCredentials: true});
  }
}
